import React, {useState} from 'react'
import { Stack, Container, Form, Button } from 'react-bootstrap'
import logo from '../Assets/L1.jpg'
import '../App.css'
import firebaseApp from './Firebase';
import {getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, signInWithRedirect,
GoogleAuthProvider} from 'firebase/auth';

const auth = getAuth(firebaseApp);
const googleProvider = new GoogleAuthProvider();

export default function Logueo() {
    const [estaRegistrandose, setEstaRegistrandose] = useState(false);

    async function submitHandler(e){
        e.preventDefault();
        const correo = e.target.formBasicEmail.value;
        const contrasea = e.target.formBasicPassword.value;
        console.log(correo, contrasea);

        if(estaRegistrandose){
            //si el usuario se registra
            const usuario = await createUserWithEmailAndPassword(auth, correo, contrasea);
            console.log(usuario);
        }else{
            //si el usuario est iniciando sesin
            signInWithEmailAndPassword(auth, correo, contrasea)
        }


        
    }

    return (
    <Container>
    <Stack gap={3}>
    <img src={logo} alt='Logo' id='logo'/>
    <h1 id='iniReg'>{estaRegistrandose ? 'Registrate' : 'Inicia Sesin'}</h1>
    <Form onSubmit={submitHandler}>
        <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" />
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" />
        </Form.Group>

        <Button variant="dark" type="submit" >
            {estaRegistrandose ? 'Registrate' : 'Inicia Sesin'}
        </Button>
    </Form>

    <Button variant="danger" type="submit" style={{ width: "300px" }} onClick={()=>signInWithRedirect(auth, googleProvider)}>
            Acceder con Google
        </Button>

        <Button
          style={{ width: "300px" }}
          variant="light" 
          onClick={() => setEstaRegistrandose(!estaRegistrandose)}
        >
          {estaRegistrandose
            ? "Ya tienes cuenta? Inicia sesin"
            : "No tienes cuenta? Regstrate"}
        </Button>

    </Stack>
</Container>
  )
}
